import React, { Component } from 'react';
import translate from './translation.js';
import ru_flag from './ru-flag.png';
import gb_flag from './gb-flag.png';
import fr_flag from './fr-flag.png';

class GeneralSkills extends Component {
    render() {
        return (
            <div className="screen">
                <h3>
                    {
                        translate(
                            {
                                'ru': ' ',
                                'en': 'General skills'
                            },
                            this.props.lang
                        )
                    }
                </h3>
                <div className="iconed-list">
                    {
                        [
                            {
                                'icon': <i className="fas fa-user-graduate"></i>,
                                'text': translate(
                                    {
                                        'ru': <div>
                                                      :
                                            <ul>
                                                <li>Python ( Django)</li>
                                                <li>C++</li>
                                                <li>JavaScript ( ReactJS)  NodeJS</li>
                                                <li>Clojure  ClojureScript</li>
                                            </ul>
                                            ... :
                                            <ul>
                                                <li> Linux</li>
                                                <li>Git</li>
                                                <li>Docker</li>
                                                <li> SQL</li>
                                                <li>     SSH</li>
                                            </ul>
                                        </div>,
                                        'en': <div>
                                            I constantly work and have a serious experience 
                                            with the following programming languages:
                                            <ul>
                                                <li>Python (especially Django)</li>
                                                <li>C/C++</li>
                                                <li>JavaScript (especially ReactJS) and NodeJS</li>
                                                <li>Clojure and ClojureScript</li>
                                            </ul>
                                            ...and technologies:
                                            <ul>
                                                <li>Linux-based OS</li>
                                                <li>Git</li>
                                                <li>SQL DBMS</li>
                                                <li>Bash and SSH</li>
                                            </ul>
                                        </div>
                                    },
                                    this.props.lang
                                )
                            },
                            {
                                'icon': <i className="far fa-compass"></i>,
                                'text': translate(
                                    {
                                        'ru': <div>
                                                      :
                                            <ul>
                                                <li>Java</li>
                                                <li>Golang</li>
                                            </ul>
                                            ... :
                                            <ul>
                                                <li>HTML5+CSS3</li>
                                                <li> NoSQL</li>
                                            </ul>
                                        </div>,
                                        'en': <div>
                                            I work from time to time and have a good experience with the following programming languages:
                                            <ul>
                                                <li>Java</li>
                                                <li>Golang</li>
                                            </ul>
                                            ...and technologies:
                                            <ul>
                                                <li>HTML5+CSS3</li>
                                                <li>NoSQL DBMS</li>
                                            </ul>
                                        </div>
                                    },
                                    this.props.lang
                                )
                            },
                            {
                                'icon': <i className="fas fa-globe"></i>,
                                'text': translate(
                                    {
                                        'ru': <div>
                                               :
                                            <table>
                                                <tbody>
                                                    <tr>
                                                        <td>
                                                            <img src={ru_flag} alt="RU"
                                                                style={{ width: 32, margin: 0, borderRadius: 0 }} />
                                                        </td>
                                                        <td>
                                                            <b>
                                                                
                                                            </b>
                                                        </td>
                                                        <td>  </td>
                                                    </tr>
                                                    <tr>
                                                        <td>
                                                            <img src={gb_flag} alt="EN"
                                                                style={{ width: 32, margin: 0, borderRadius: 0 }} />
                                                        </td>
                                                        <td>
                                                            <b>
                                                                
                                                            </b>
                                                        </td>
                                                        <td>   ,    </td>
                                                    </tr>
                                                    <tr>
                                                        <td>
                                                            <img src={fr_flag} alt="FR"
                                                                style={{width: 32, margin: 0, borderRadius: 0}}/>
                                                        </td>
                                                        <td>
                                                            <b>
                                                                
                                                            </b>
                                                        </td>
                                                        <td>   ,  </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>,
                                        'en': <div>
                                            I am familiar with the following languages:
                                            <table>
                                                <tbody>
                                                    <tr>
                                                        <td>
                                                            <img src={ru_flag} alt="RU"
                                                                style={{ width: 32, margin: 0, borderRadius: 0 }} />
                                                        </td>
                                                        <td>
                                                            <b>
                                                                Russian
                                                            </b>
                                                        </td>
                                                        <td>My native language</td>
                                                    </tr>
                                                    <tr>
                                                        <td>
                                                            <img src={gb_flag} alt="EN"
                                                                style={{ width: 32, margin: 0, borderRadius: 0 }} />
                                                        </td>
                                                        <td>
                                                            <b>
                                                                English
                                                            </b>
                                                        </td>
                                                        <td>I read, write and communicate in English easily</td>
                                                    </tr>
                                                    <tr>
                                                        <td>
                                                            <img src={fr_flag} alt="FR"
                                                                style={{ width: 32, margin: 0, borderRadius: 0 }} />
                                                        </td>
                                                        <td>
                                                            <b>
                                                                French
                                                            </b>
                                                        </td>
                                                        <td>I can read, write and support a conversation in French</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    },
                                    this.props.lang
                                )
                            }
                        ].map(
                            (elt, idx) => (
                                <div className="iconed-list-item" key={ "iconed-list-item-" + (idx + 1) }>
                                    <div className="iconed-list-item-icon">
                                        { elt.icon }
                                    </div>
                                    <div className="iconed-list-item-text">
                                        { elt.text }
                                    </div>
                                </div>
                            )
                        )
                    }
                </div>
            </div>
        );
    }
}

export default GeneralSkills;



